<template>
  <div class="catalogue">
    <div class="top">目录</div>
    <div class="book">
      <div class="pic">
        <img :src="`http://statics.zhuishushenqi.com${currentBook.cover}`" />
      </div>
      <div class="info">
        <div class="title">{{ currentBook.title }}</div>
        <div class="author">{{ currentBook.author }}</div>
        <div class="count">
          共 <span>{{ count }}</span> 章
        </div>
      </div>
    </div>
    <van-skeleton :row="17" :loading="loading" row-width="180px">
      <ul class="cata">
        <li
          :class="{
            active: active
              ? active == chapter.title
              : bookIndex + 1 == chapter.order,
          }"
          v-for="(chapter, index) in chapters"
          :key="index"
          @click.stop="toChapter(index)"
        >
          {{ chapter.title }}
        </li>
      </ul>
    </van-skeleton>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Catalogue",
  props: ["count", "chapters", "loading", "active"],
  computed: {
    ...mapState(["currentBook"]),
    bookIndex() {
      var id = this.$route.query.id;
      return Number(localStorage.getItem(id));
    },
  },
  methods: {
    toChapter(index) {
      this.$emit("close-all");
      if (this.$route.name == "BookDetail") {
        this.$router
          .push(`/chapter?id=${this.currentBook._id}&index=${index}`)
          .catch((err) => err);
      } else {
        this.$router
          .replace(`/chapter?id=${this.currentBook._id}&index=${index}`)
          .catch((err) => err);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.catalogue {
  // background-color: rgba(222, 222, 222, 0.5);
  padding-top: 15px;
  .van-skeleton {
    margin-top: 10px;
  }
  .top {
    font-size: 20px;
    margin-left: 15px;
  }
  .book {
    display: flex;
    margin-top: 20px;
    margin-left: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
    .pic {
      flex-basis: 30%;
      img {
        display: block;
        width: 100%;
      }
    }
    .info {
      flex: 1;

      font-size: 14px;
      margin: 0 20px 0 5px;
      padding-bottom: 20px;

      .title {
        word-wrap: break-word;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1; /*行数*/
        -webkit-box-orient: vertical;
      }
      .author {
        font-size: 12px;
        color: #b93321;
        margin: 10px 0;

        word-wrap: break-word;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1; /*行数*/
        -webkit-box-orient: vertical;
      }
    }
  }
  .cata {
    margin-left: 20px;
    margin-top: 10px;
    padding-bottom: 20px;
    font-size: 14px;
    li {
      line-height: 28px;
      margin-right: 20px;

      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1; /*行数*/
      -webkit-box-orient: vertical;
      &.active {
        color: red;
      }
    }
  }
}
</style>